.App {
  text-align: center;
}

body {
  background: #333; /* Dark background for contrast */
  color: #fff; /* White text for readability */
  font-family: Arial, sans-serif; /* Modern font */
}

/* Responsive table styles */
.table-container {
  overflow-x: auto; /* Allows table to scroll on small devices */
}

table {
  border-collapse: collapse;
  width: 100%; /* Full width tables */
  margin-bottom: 20px; /* Space between tables */
}

th, td {
  border: 1px solid #777; /* Lighter border for cells */
  text-align: left;
  padding: 8px; /* Padding for content inside cells */
}

th {
  background-color: #555; /* Slightly lighter header for depth */
}

tr:nth-child(even) {
  background-color: #2c2c2c; /* Zebra-striping for rows */
}

/* Improved input styles */
input[type='number'] {
  background-color: #222; /* Dark background */
  border: 1px solid #555; /* Subtle border */
  border-radius: 4px; /* Rounded corners */
  color: #ddd; /* Light text color for contrast */
  font-size: 1rem; /* Adequate font size */
  padding: .5rem; /* Comfortable padding */
  transition: all 0.3s ease-in-out; /* Smooth transition for interactions */
}

input[type='number']:focus {
  outline: none; /* Remove outline */
  border-color: #61dafb; /* Highlighted border color */
  box-shadow: 0 0 5px rgba(97, 218, 251, 0.5); /* Subtle glow effect */
}

/* Remove default arrows from number inputs in Webkit browsers */
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number']:hover {
  border-color: #777; /* Slightly lighter border on hover */
}

/* Button styles */
button {
  background-color: #4CAF50; /* Green background */
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

button:hover {
  background-color: #45a049; /* Darker green on hover */
}

/* Media queries for responsive design */
@media only screen and (max-width: 768px) {
  input[type='number'] {
    font-size: 0.8rem; /* Smaller font size for small screens */
  }

  table, th, td {
    padding: 4px; /* Less padding for small screens */
  }

  button {
    padding: 8px 16px; /* Smaller button on small screens */
    font-size: 14px; /* Smaller font size for button */
  }
}

/* Clearfix */
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
